import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BaseTable } from '../../../interface/BaseTable';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
@Component({
  selector: 'app-house',
  templateUrl: './house.component.html',
  styleUrls: ['./house.component.less']
})
export class HouseComponent implements OnInit {
  // DIALOG 模块
  BaseTablevisible = false;
  listOfData = [];
  isOperating = false;
  SelectData = [];
  editID = '';
  editItem = '';
  isAllDisplayDataChecked = false;
  isIndeterminate = false;
  listOfDisplayData = [];
  mapOfCheckedId = {};
  public TableModal: FormGroup;
  constructor(private http: HttpClient, private fb: FormBuilder) {
    console.log('执行Constructor')
    this.TableModal = this.fb.group({
      formname: ['', [Validators.required]],
      formxiangmu: ['', [Validators.required]],
      formdaili: ['', [Validators.required]],
      formquyu: ['', [Validators.required]],
      formPeo: ['', [Validators.required]],
      formtel: ['', [Validators.required]],
      formhouse: ['', [Validators.required]],
      zhuangtai: ['', [Validators.required]]
    });
  }
  addData: any;
  tableDatas: BaseTable = {
    // 所有的详情数据
    dataSet: [],
    total: 0,
    itemLength: 0,
    title: '✈东南亚航空管理后台✈✈✈',
    footer: '✈✈✈✈✈✈✈✈✈✈',
    loading: false,
    bordered: true,
    checked: true,
    checkedSingle: false,
    switch: true,
    switchName: 'zhuangtai',
    size: 'default',
    pagination: true,
    pageSize: 5,
    pageIndex: 2,
    operation: false,
    dataTh: [
      {
        title: '运营方'
      },
      {
        title: '项目名称'
      },
      {
        title: '项目代理'
      },
      {
        title: '项目代理区域'
      },
      {
        title: '项目代理人'
      },
      {
        title: '项目管理人电话'
      },

      {
        title: '楼盘名称'
      },
      {
        title: '项目当前状态'
      },
      {
        title: '操作'
      }
    ],
    tdItem: [
      {
        name: 'formname'
      },
      {
        name: 'formxiangmu'
      },
      {
        name: 'formdaili'
      },
      {
        name: 'formquyu'
      },
      {
        name: 'formPeo'
      },
      {
        name: 'formtel'
      },
      {
        name: 'formhouse'
      }
    ]
  };
  ngOnInit(): void {
    console.log('执行2222222222222222222222222222222');
    
    this.getData();
  }

  getData() {
    this.isOperating = true;
    const url = 'http://localhost:9562/admin/getFLM';
    // subscribe: 利用rxjs封装的http获取异步请求的数据，类似于promise
    this.http.get(url).subscribe((respone: any) => {
      //  data:返回的数据
      const data = respone.data;
      // const data1 = JSON.parse(JSON.stringify( this.tableDatas));
      const data1 = Object.assign({}, this.tableDatas);
      data1.dataSet = data;
      this.tableDatas = data1;
      this.isOperating = false;
    });
  }

  checkAll(value: boolean): void {
    this.refreshStatus();
  }
  refreshStatus(): void {}
  handleCancel() {
    console.log('取消');
    this.BaseTablevisible = false;
  }
  handleOk() {
    console.log('确定');
    this.BaseTablevisible = false;
  }
  editTb(e) {
    this.BaseTablevisible = true;
    this.editID = e._id;
    this.editItem = e;
    this.TableModal.patchValue(e);
  }
  closeModel(e) {
    this.BaseTablevisible = false;
  }
  saveForm(e) {
    for (const i in this.TableModal.controls) {
      if (i) {
        this.TableModal.controls[i].markAsDirty();
        this.TableModal.controls[i].updateValueAndValidity();
      }
    }
    // 判读是否有效
    if (this.TableModal.status === 'VALID') {
      // 判断是编辑还是....
      // if (this.isEdit) {
      this.updatePartner();
      // } else {
      //   this.createPartner();
      // }
    }
  }
  // 修改数据信息。 数据的双向绑定
  updatePartner() {
    const reqData: any = {
      formname: this.getOperationDateFormValue('formname'),
      formxiangmu: this.getOperationDateFormValue('formxiangmu'),
      formdaili: this.getOperationDateFormValue('formdaili'),
      formquyu: this.getOperationDateFormValue('formquyu'),
      formPeo: this.getOperationDateFormValue('formPeo'),
      formtel: this.getOperationDateFormValue('formtel'),
      formhouse: this.getOperationDateFormValue('formhouse'),
      zhuangtai: this.getOperationDateFormValue('zhuangtai')
    };
    for (const key in reqData) {
      if (reqData.hasOwnProperty(key)) {
        const element = reqData[key];
        this.editItem[key] = element;
      }
    }
    this.BaseTablevisible = false;
  }

  private getOperationDateFormValue(str) {
    return this.TableModal.get(str).value;
  }
  checked($event) {
    this.SelectData = $event;
  }
  checkedSingle($event) {
    console.log('执行1');
    console.log($event);
  }
  pageindex($event) {
    this.tableDatas.pageIndex = $event;
  }
  DeleteData() {
    let newData = this.tableDatas.dataSet.filter(item => {
      let isDel = false;
      for (let i = 0; i < this.SelectData.length; i++) {
        if (this.SelectData[i]._id === item._id) {
          isDel = true;
          break;
        }
      }
      if (!isDel) {
        return item;
      }
    });
    let newData1 = JSON.parse(JSON.stringify(newData));
    this.tableDatas.dataSet = newData1;
  }
}
