import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
// 注册当前路由
import { HouseRoutingModules } from './houseManage-routing.module';
// 导入子页面组件
import { HouseComponent } from './House/house.component';

// 引入面包屑
import { SharedModule } from 'src/app/components/shared.module';
// 配置公共组件
import { CommonCoModule } from 'src/app/common/common.component';

@NgModule({
  declarations: [HouseComponent],
  imports: [CommonModule, SharedModule, CommonCoModule],
  exports: [HouseRoutingModules],
  providers: []
})
export class HouseManageModule {}
